.modal-ui {
  z-index: 300;
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  opacity: 0;
  pointer-events: none;
  transition: all 300ms ease;
  &.is-active {
    opacity: 1;
    pointer-events: auto;
    z-index: 400
  }
  &__overlay {
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.5);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &__inner {
    width: 50rem;
    background-color: #d8d8d8;
  }
  &__header {
    background-color: #989898;
    height: 4.8rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 1rem;
    h3 {
      font-size: 2rem;
      color: #fff;
    }
  }
  &__filter {
    display: flex;
  }
  &__btn-filter {
    width: 8rem;
    height: 3rem;
    border: 0;
    background-color: #fff;
    text-align: center;
    line-height: 3rem;
    font-size: 1.4rem;
    color: #525252;
    border-radius: 3rem;
    &.is-active {
      background-color: #1b97c8;
      color: #fff;
    }
  }
  &__filter-group {
    margin: 0 0.4rem;
    position: relative;
  }
  &__sub-nav {
    position: absolute;
    width: 9.2rem;
    left: 0;
    top: 3.8rem;
    z-index: 100;
    opacity: 0;
    transition: all 300ms ease-in-out;
    pointer-events: none;
    &.is-active {
      opacity: 1;
      pointer-events: auto;
    }
    button {
      display: block;
      width: 9.2rem;
      height: 3.2rem;
      line-height: 3.2rem;
      border: solid 0.1rem #979797;
      text-align: center;
      font-size: 1.4rem;
      color: #525252;
      background-color: #fff;
      transition: all 300ms ease;
      border-bottom: 0;
      &:last-child {
        border-bottom: solid 0.1rem #979797;
      }
      &:hover {
        background-color: #d7d7d7;
      }
      &.is-active {
        background-color: #d7d7d7;
        pointer-events: none;
      }
    }
  }
  &__btn-close {
    width: 1.8rem;
    background-color: transparent;
    height: 1.8rem;
    position: relative;
    border: 0;
    &::before,
    &::after {
      width: 2rem;
      height: 0.2rem;
      background-color: #fff;
      position: absolute;
      content: "";
      left: 50%;
      top: 50%;
    }
    &::before {
      transform: translate3d(-50%,-50%,0) rotate(45deg);
    }
    &::after {
      transform: translate3d(-50%,-50%,0) rotate(-45deg);
    }
  }
  &__content {
    padding: 3.6rem;
    h4 {
      font-size: 2rem;
      color: #1b97c8;
      margin-right: 2rem;
    }
  }
  &__iv {
    margin-bottom: 4rem;
  }
  &__iv-list {
    display: flex;
    flex-wrap: wrap;
  }
  &__row {
    display: flex;
    margin-bottom: 1.6rem;
    align-items: center;
    &.center {
      justify-content: center;
      gap: 2rem;
      button {
        margin-left: 0;
        margin-right: 0;
      }
    }
    & > label {
      width: 12rem;
      font-size: 2rem;
      color: #000;
      line-height: 4.5rem;
    }
    & > input[type=text] {
      background-color: #fff;
      border-radius: 0.5rem;
      border: 0;
      width: 29rem;
      box-sizing: border-box;
      color: #000;
      font-size: 2rem;
      line-height: 4.5rem;
      height: 4.5rem;
      padding: 0 1rem;
    }
    & > input[type=time] {
      background-color: #fff;
      border-radius: 0.5rem;
      border: 0;
      width: 16rem;
      box-sizing: border-box;
      color: #000;
      font-size: 1.4rem;
      line-height: 2.4rem;
      height: 2.4rem;
      padding: 0 1rem;
    }
  }
  &__text {
    font-size: 1.6rem;
    line-height: 1.6;
    color: #000;
    strong {
      padding: 0 0.6rem;
      color: #1b97c8;
    }
  }
  &__employee-list,
  &__office-list {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 2rem;
  }
  &__btn-name,
  &__btn-office,
  &__btn-iv {
    margin-right: 0.8rem;
    text-align: left;
    font-size: 1.6rem;
    color: #000;
    border: solid 0.1rem transparent;
    padding: 0.4rem 0.6rem;
    background-color: transparent;
    transition: all 300ms ease;
    &:active {
      border-color: #1b97c8;
      color: #1b97c8;
    }
    &.is-active {
      border-color: #1b97c8;
      color: #1b97c8;
    }
  }
  &__btn-next,
  &__btn-confirm,
  &__btn-cancel,
  &__btn-delete {
    background-color: #1b97c8;
    width: 12rem;
    text-align: center;
    line-height: 4rem;
    height: 4rem;
    text-align: center;
    border-radius: 4rem;
    color: #fff;
    border: 0;
    font-size: 1.8rem;
    margin: 0 auto;
    display: block;
    transition: all 300ms ease;
    &:active {
      background-color: #b6b6b6;
    }
  }
  &__btn-cancel,
  &__btn-delete {
    color: #111;
    background-color: #fff;
    &:active {
      background-color: #fff;
    }
  }
  &__checkbox-container {
    width: auto !important;
    display: block;
    position: relative;
    padding-left: 3rem;
    cursor: pointer;
    font-size: 1.4rem !important;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    line-height: 1.6 !important;
    input {
      position: absolute;
      opacity: 0;
      cursor: pointer;
      height: 0;
      width: 0;
    }
    .checkmark {
      position: absolute;
      top: 0;
      left: 0;
      height: 2rem;
      width: 2rem;
      background-color: #fff;
      border: solid 0.1rem #999;
      &::after {
        content: "";
        position: absolute;
        display: none;
      }
    }
    input:checked ~ .checkmark {
      background-color: #1b97c8;
      border-color: #1b97c8;
    }
    input:checked ~ .checkmark:after {
      display: block;
    }
    .checkmark {
      &::after {
        left: 0.8rem;
        top: 0.4rem;
        width: 0.4rem;
        height: 0.6rem;
        border: solid white;
        border-width: 0 2px 2px 0;
        -webkit-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
        transform: rotate(45deg);
      }
    }
  }
  &__time {
    font-size: 1.4rem;
    color: #000;
    strong {
      padding: 0 0.8rem;
      color: #1b97c8;
    }
  }
}